*{
    margin:0px;
    padding: 0px;
}
:root{
    --primary-color: #207398;
}

body{
    background-color: rgb(255,255,255) !important;
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
}

.layout{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.sidebar{
    width:20%;
    background-color: var(--primary-color);
    min-height: 100vh;
    box-sizing: border-box;
}

.logo{
    width: 90px;
    height: 90px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 90px;
    background-color: #fff;
    margin: 20px auto;
}

.content{
    width: 80%;
}

.header{
    width:100%;
    background-color: var(--primary-color);
    text-align: right;
    padding: 10px 20px 10px 0;
    box-sizing: border-box;
    color: #fff;
}

.menu{
    width: 50%;
    color: #fff;
    margin:20px auto;
    text-align: left;
}

.sidebarList{
    margin-top: 50px;
}


.title{
    padding-left: 1.5rem;
}

.title:hover{
    text-decoration: underline;
    transition: 5s ease-in-out;
    cursor: pointer;
}

.active{
    background-color: rgb(4, 59, 92);
     padding: 0.2rem 0 !important;
}

a{
    color: #fff !important;
}

.text_center{
    width: 100%;
    text-align: center;
    margin: 20px 0;
}

h4{
    font-weight: 700 !important;
}

.buttons{
    width:250px;
    display: flex;
    justify-content: space-between;
    align-items: center;

}
.buttons .success{
    background-color :green;
    color: #fff;
    height: 30px !important;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 0.8rem;
    line-height: 20px;
}

.buttons .danger{
    background-color :red;
    color: #fff;
    height: 30px !important;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 0.8rem;
    line-height: 20px;
}

.input_style{
    box-sizing: border-box;
    margin: 0;
    padding: 4px 11px;
    color: rgba(0, 0, 0, 0.88);
    font-size: 14px;
    line-height: 1.5714285714285714;
    list-style: none;
    position: relative;
    display: inline-block;
    min-width: 0;
    background-color: #ffffff;
    background-image: none;
    border-width: 1px;
    border-style: solid;
    border-color: #d9d9d9;
    border-radius: 6px;
    transition: all 0.2s;
    width:45%;
    margin-right: 5px;
}