/* @tailwind base;
@tailwind components;
@tailwind utilities; */

*{
    margin:0px;
    padding: 0px;
}

:root{
    --primary-color: #207398;
}

body{
    background-color: rgb(255,255,255) !important;
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
}

h1{
    font-size: 2rem !important;
    font-weight: 600;
}

h2{
    font-size: 1.5rem !important;
    font-weight: 600;
}

h3{
    font-size: 1.25rem !important;
    font-weight: 600;
}

h4{
    font-size: 1rem !important;
    font-weight: 400;
}

.headContainer{
    width: 100%;
    display: flex;
    padding: 10px 5vw 0 5vw;
    box-sizing: border-box !important;
    justify-content: space-between;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
}
.Navbar{
    width:50%;
}
.navbar{
    width: 100%;
    display: flex;
    justify-content: space-between;
    text-align: center;
}
ul{
    width: 100%;
}
ul li {
    list-style: none;
}

a{
     text-decoration: none;
}
.anchor{
    color:#7f8c8d !important;
}

.account button{
    background-color: transparent;
    border: 1px solid rgb(0,0,0);
    padding: 5px 10px;
    margin: 0 5px;
}

.account button:hover{
    color: #fff !important;
}
.banner{
    background-image: url(../img/bannerBg.jpg);
    min-height: 90vh;
    min-width: 100%;
    background-size: cover;
    /* margin: 0 */
    opacity: 0.2;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -20;
    overflow: hidden;
    box-sizing: border-box;
}

.banner_content{
    width: 85vw;
    min-height: 80vh;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
}

.banner_content .image{
    width: 50%;
    height: 80vh;
    position: relative;
}

.banner_content .image img{
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -15;
}

.login, .signup{
    color:rgb(0,0,0) !important;
}

.banner_content .content{
    padding-left: 2rem;
}

.book{
    border:none;
    background-color: var(--primary-color);
    color:#fff;
    width: 250px !important;
    height: 50px !important;
    line-height: 50px;
    border-radius: 50px;
    text-align: center;
    cursor: pointer;
    margin: 50px 0;
}

.account button:hover{
    border:none;
    background-color: var(--primary-color) !important; 
    cursor: pointer;
}

.account button:hover a{
    color:#fff !important;
}

.book:hover{
    transform: translateY(-10px);
    background-color: var(--primary-color);
}

.Services{
    width: 80vw;
    display: flex;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    transform: translateY(-150px);
    min-height: 300px;
    margin: auto;
    padding: 30px 20px;
    justify-content: space-evenly;
}
.para{
    width: 300px;
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 0 25px;
    justify-content: center;
}
.para img{
    width: 200px;
    position: absolute;
    top:0;
    left: 0;
    height: auto;
    opacity: 0.1;
}

.callUs{
    border:none;
    background-color: var(--primary-color);
    color:#fff;
    width: 200px !important;
    height: 40px !important;
    line-height: 40px;
    border-radius: 40px;
    text-align: center;
    cursor: pointer;
    margin: 20px 0;
}

.aboutArticle{
    width: 80vw;
    display: flex;
    margin: auto;
    justify-content: space-between;
}

.sideimage{
    width: 50%;
    position: relative !important;
}

.lowersection{
    overflow: hidden;
    box-sizing: border-box;
    border-radius: 10px;
    position: absolute;
    height: 450px;
    width: 450px;
}

.upperSection{
    background-color: var(--primary-color);
    border-radius:10px;
    transform: translate(200px, 200px);
    position: absolute;
    color: rgb(255,255,255);
    padding:35px 30px 20px 30px;
}

.upperSection h2{
    padding-bottom:15px;
}

.underline{
    height: 1px;
    background-color: #fff;
    width: 100px;
    opacity:0.1;
}

table{
    border: none;
    outline: none;
}

table td{
    padding: 5px;
    font-size: 14px;
}

.aboutContent{
    width: 600px;
    float: right;
    margin-left: 100px;
    word-spacing:5px;
}

.aboutContent p{
    padding: 20px 0;
}

.aboutContent h4{
    width: 100%;
    text-align: left !important;
}

.aboutContent h4::after{
    content: '';
    min-width: 20px !important;
    min-height: 5px !important;
    border-radius: 5px;
    background-color: coral;
    display: inline-block;
    margin-left: 5px;
}

.facilities h4::before{
    content: '';
    min-width: 20px !important;
    min-height: 5px !important;
    border-radius: 5px;
    background-color: coral;
    display: inline-block;
    margin-right: 5px;
}

.facilities{
    width: 100%;
    margin-top: 250px;
    background-color: #ecf0f1;
    padding: 20px 0;
}

.facilities h4{
    width: 100%;
    text-align: center;
    padding:20px 0;
}

.facilities h1{
    width: 100%;
    color: var(--primary-color);
    text-align: center;
}

.facilities h4::after{
    content: '';
    min-width: 20px !important;
    min-height: 5px !important;
    border-radius: 5px;
    background-color: coral;
    display: inline-block;
    margin-left: 5px;
}

.facilities p{
    width: 800px;
    margin: 20px auto;
    text-align: center;
    color: #2d3436;
}

.layer{
    width: 80%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 25px;
    margin: 20px auto;
}

.facility{
    background-color: rgb(255, 255, 255);
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
    padding: 25px 0;
}

.facility i{
    font-size: 2.5rem;
    padding: 15px 0;
    color:var(--primary-color)
}

.facility h2{
    padding-top:1.5rem;
}

.dotted-circle{
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
    border-radius: 100px;
    border: 2px dotted var(--primary-color);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.FooterContainer{
    width: 100%;
    margin: auto;
    background-color: var(--primary-color);
}

.Footer{
    width: 80%;
    color:rgb(255, 255, 255);
    display: grid;
    margin: auto;
    grid-template-columns: repeat(3, 1fr);
}

.Footer h2{
    padding: 20px 0;
}

.info table{
    width: 50%;
}

.info td i{
    font-size: 1.5rem;
}

.Ftimings > table{
    width: 50%;
}

.lowerFooter{
    width: 100%;
    background-color: rgb(0,0,0);
    color: #fff;
    padding: 10px 0;
    text-align: center;
}

.Signup, .Login{
    min-height: 100vh;
    min-width: 100vw;
    background-color: #ecf0f1;
    display: flex;
    align-items: center;
}

.Signup_form, .Login_form{
    width:800px;
    display: flex;
    align-self: center;
    justify-self:center;
    margin: auto;
    background-color: rgb(255, 255, 255);
    padding: 25px;
    border-radius: 15px;
    justify-content: space-between;
}

.Signup_left{
    width: 300px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.Signup_right{
    width: 450px
}
.login_left{
    width: 350px;
}
.login_right{
    width: 450px;
}
.login_right form{
    width: 100%;
}
.Signup_right button, .login_right button{
    background-color: #6c5ce7;
    color: rgb(255,255,255);
    padding: 10px 25px;
    border-radius: 5px;
    text-align: center;
    cursor: pointer;
}

.spinner{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.astyle{
    color: blue !important;
    text-decoration: underline !important;
    margin-top:20px !important;
}